}
/* nav */
.nav {
  font-size: 13px;
  overflow: hidden;
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background-color: #f7f7f7;
  border-bottom: 1px #ddd solid;
  z-index: 1500;
}
.nav-1200 {
  width: 80vw;
  margin: 0 auto;
  min-width: 1200px;
  display: flex;
  justify-content: space-between;
}
.userBox > span:hover {
  color: red;
}
.left-ul li,
.right-ul li {
  float: left;
}
.icon {
  display: inline-block;
}
.line {
  line-height: 29px;
  color: #c6c6c6;
  margin: 0 10px;
}
.loginBox a {
  color: #cd0010;
}

/* logo */
.tabBox {
  height: 115px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logoBox,
.logoBox img {
  width: 500px;
}
.codeBox,
.codeBox img {
  width: 88px;
}

/*  */
.searchBox {
  display: flex;
  background-color: #e70012;
  height: 130px;
}
.mid-search {
  margin-top: 15px;
}
/* menu */
.menuBox {
  width: 230px;
}
.menuBox:hover .first-menu {
  display: block;
}
ul.firstShow {
  display: block;
}
.menu-p {
  width: 230px;
  height: 130px;
  line-height: 130px;
  background-color: #cd0010;
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "\5FAE\8F6F\96C5\9ED1";
  font-weight: bold;
}
.white-tri {
  width: 9px;
  height: 4px;
}
.show-menu {
  position: relative;
  z-index: 1400;
}
.first-menu {
  background: #fff;
  position: relative;
  border: 1px #d4d4d4 solid;
  display: none;
  padding: 10px 0;
  box-sizing: border-box;
  font-size: 14px;
  height: 345px;
}
.firstLi {
  padding-left: 15px;
  padding-right: 15px;
  text-align: left;
  height: 30px;
  line-height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.right-tri {
  width: 7px;
  height: 12px;
  background: url("../../assets/imgs/right-tri.png") no-repeat;
}
.firstLi:hover {
  background-color: #cd0010;
  color: white;
  cursor: pointer;
}
.firstLi:hover .right-tri {
  background: url("../../assets/imgs/right-tri2.png") no-repeat;
}
.first-menu:hover .second-menu {
  display: block;
}

.second-menu {
  display: none;
  position: absolute;
  left: 230px;
  top: 0;
  width: 990px;
  min-height: 430px;
  border: 2px #cd0010 solid;
  background-color: #ffffff;
  padding: 10px;
}
.second-menu > li {
  /* height: 18px; */
  line-height: 18px;
  text-indent: 6px;
  color: #3c3c3c;
  font-size: 12px;
  text-align: left;
  /* margin-bottom: 5px; */
}
.line2 {
  color: #c6c6c6;
  margin: 0 5px;
}
.second-t {
  color: #555;
  font-weight: 600;
  margin-right: 5px;
  margin-bottom: 5px;
}
.third-t {
  color: #333;
  margin-right: 5px;
  display: block;
}
.second-t:hover,
.third-t:hover {
  text-decoration: underline;
  cursor: pointer;
}
.second-content {
  display: flex;
  flex-wrap: wrap;
  font-size: 13px;
  padding: 5px;
}
/* icon */
.search-icon-box {
  width: 110px;
  line-height: 130px;
}
.search-icon {
  width: 35px;
}
.hot {
  display: flex;
  color: white;
  font-size: 14px;
  margin-top: 10px;
}
.hot li {
  margin-right: 10px;
}
.p-hot {
  color: red;
  background-color: #fff;
  font-size: 10px;
  line-height: 20px;
  height: 20px;
  padding: 0 5px;
  margin-right: 10px;
}
.hot > li:hover {
  text-decoration: underline;
}
.bar-box {
  margin-top: 5px;
  display: flex;
  font-size: 12px;
  height: 30px;
  line-height: 30px;
}
.search-input {
  width: 500px;
  text-indent: 6px;
  font-size: 12px;
}
.search-key {
  background: #e4e4e4;
  width: 80px;
  text-indent: 2px;
}
.search-btn {
  color: white;
  font-weight: 600;
  width: 87px;
  background-color: #cd0010;
}
</style>



