<script>
import myhead from "./common/header.vue";
export default {
  name: "About",
  components: {
    myhead
  },
  data() {
    return {
      //tab0->1->
      curIndex:0
    };
  },
  methods: {
    changeCurIndex(i){
      this.curIndex = i;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bar{
  height: 30px;
  line-height: 30px;
  text-align: left;
  font-size: 14px;
  padding: 0 10%;
  margin: 20px 0;
}
.bar>span{
  margin: 0 10px;
}
.main{
  padding: 0 10%;
  display: flex;
  text-align: left;
}
.left{
  border-top: 1px #ddd solid;
  font-size: 16px;
  width: 230px;
  min-height: 750px;
  text-align: right;
  padding-top: 50px;
  padding-right: 15px;
}
.left-p{
  margin-bottom: 43px;
}
.left>p:hover{
  text-decoration: underline;
  color: #cd000f;
}
.left-active{
  color: #cd000f;
  font-weight: 600;
}
.division{
  border-top: 1px #ddd solid;
}
.right{
  padding-top:50px; 
  border-top: 1px #ddd solid;
  width: 845px;
  padding-left: 100px;
}
.h{
  background-color: black;
  color: #ddd;
  font-size: 15px;
  height: 35px;
  line-height: 35px;
}
.big-title{
