  font-weight: 600;
  padding: 4px 20px;
  margin: 7px 0;
  border: 1px dashed #009845;
}
.intro-p{
  font-size: 14px;
  margin: 0 10px;
  line-height: 1.5;
}
.box{
  margin-top: 20px;
  background-color: #eaeaea;
  padding: 20px 20px;
}
.box-title{
  font-weight: 600;
  font-size: 18px;
}
.box-intro{
  display: flex;
  margin-top: 10px;
}
.box-img>img{
  width: 100px;
  height: 100px;
}
.box-t{
  font-size: 14px;
  width: 650px;
  padding-left: 20px;
  line-height: 1.5;
}
.range{
  padding: 20px 20px;
}
.range>img{
  width: 761px;
}
.redtri{
  float: left;
  margin-right: 15px;
  border-top-width: 14px;
  border-right-width: 12px;
  border-bottom-width: 20px;
  border-left-width: 14px;
  border-style:solid;
  border-color:#cd000f transparent transparent #cd000f;
}
</style>



